import ReactGA from 'react-ga';

function Tracker() {

  useEffect(() => {
    ReactGA.initialize("G-SY3ZL5E16J");
    // To Report Page View 
    ReactGA.pageview(window.location.pathname + window.location.search);
  }, [])

  useEffect(() => {
   console.log(window.location.pathname)
  })
return null
  
}

export default Tracker;